import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { environment } from '../../environments/environment';

@Injectable()
export class QueryTypeService {

  private BASE_URL = environment.serverUrl + 'queryCategory/';

  constructor(private http: HttpClient) { }


  create(name: string): Observable<any> {
    console.log('reached service');
    return this.http.post(this.BASE_URL, { name })
      .map(response => this.reloadList());
  }

  update(id: string, name: string): Observable<any> {
    return this.http.put(this.BASE_URL, { id, name })
      .map(response => this.reloadList());
  }

  delete(id: string): Observable<any> {
    return this.http.delete(this.BASE_URL + id)
      .map(response => this.reloadList());
  }

  fetch(): Observable<any> {
    return this.http.get(this.BASE_URL)
      .map(response => {
        console.log(response);
      });
  }

  private reloadList() {
    console.log('reload query type list');
  }
}



